import mongoose from 'mongoose'
import User from '../model/User'
import Storecommodity from '../model/Storecommodity'
import Commentstore from '../model/Commentstore'
import Store from '../model/Store'
import multer from 'multer'

const upload = multer({//staticupload
    dest:'./static/upload'
})

const storecommodityAPIs = (app)=>{

    //
    app.post('/api/storecommodities',upload.array('pictures'),async (req,res)=>{
        try {
            const {username,token,storename,s_token,category,price,title,content,filelist} = req.body
            console.log("fileList:",filelist)
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!storename){
                return res.status(400).json({
                    message:""
                })
            }
            if(!s_token){
                return res.status(400).json({
                    message:"s_token"
                })
            }
            if(!category){
                return res.status(400).json({
                    message:""
                })
            }
            if(!price){
                return res.status(400).json({
                    message:""
                })
            }
            if(!title){
                return res.status(400).json({
                    message:""
                })
            }
            if(!content){
                return res.status(400).json({
                    message:""
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const store = await Store.findOne({
                storename:storename,
                s_token:s_token
            })
            if(!store){
                return res.status(400).json({
                    message:"s_token"
                })
            }
            const fl = filelist.flat(Infinity);
            console.log(fl)
           
            const newCommodity = {//
                category:category,
                price:price,
                title:title,
                content:content,
                author:store //
            }
            const commodity = new Storecommodity(newCommodity)//
            commodity.pictures = fl
            await commodity.save()//saveawait
            store.storecommodities.push(commodity)
            await store.save()
            user.cinformations.push(commodity)
            await user.save()
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
        
    }),

    //
    app.get('/api/storecommodities',async (req,res)=>{
        try {
            const commodities = await Storecommodity.find()
            .populate('author','storename avatar')//findone,populate
            .sort({"posttime":-1})
            return res.json({
                data:commodities
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),

    //
    app.get('/api/storecommodities/:cid',async (req,res)=>{
        try {
            const {cid} = req.params
            if(!cid){
                return res.status(400).json({
                    message:"ID"
                })
            }
            const commodity = await Storecommodity.findById(cid)
            .populate('author','storename avatar description')
            if(!commodity){
                return res.status(400).json({
                    message:""
                })
            }
            const comments = await Commentstore.find({target:cid})
            .populate('author','username avatar description nickname')//
            .populate('dianzans_storecommodity')
            return res.json({
                data:{
                    commodity:commodity,
                    comments:comments
                }
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),
    //
    app.patch('/api/storecommodities/:cid',upload.array('pictures',9),async (req,res)=>{
        try {
            const {cid} = req.params
            if(!cid){
                return res.status(400).json({
                    message:"ID"
                })
            }
            const {username,token,storename,s_token,category,price,title,content} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!storename){
                return res.status(400).json({
                    message:""
                })
            }
            if(!s_token){
                return res.status(400).json({
                    message:"s_token"
                })
            }
            if(!category){
                return res.status(400).json({
                    message:""
                })
            }
            if(!price){
                return res.status(400).json({
                    message:""
                })
            }
            if(!title){
                return res.status(400).json({
                    message:""
                })
            }
            if(!content){
                return res.status(400).json({
                    message:""
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const store = await Store.findOne({
                storename:storename,
                s_token:s_token
            })
            if(!store){
                return res.status(400).json({
                    message:"s_token"
                })
            }
            const commodity = await Storecommodity.findById(cid)
            if(!cid){
                return res.status(400).json({
                    message:""
                })
            }
            if(String(store._id)!==String(commodity.author)){
                return res.status(400).json({
                    message:""
                })
            }
            commodity.category = category
            commodity.price = price
            commodity.title = title
            commodity.content = content
            await commodity.save()
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),
    //
    app.delete('/api/storecommodities/:cid',async (req,res)=>{
        try {
           const {cid} = req.params
           if(!cid){
               return res.status(400).json({
                   message:"ID"
               })
           }
           const{username,token,storename,s_token} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!storename){
                return res.status(400).json({
                    message:""
                })
            }
            if(!s_token){
                return res.status(400).json({
                    message:"s_token"
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const store = await Store.findOne({
                storename:storename,
                s_token:s_token
            })
            if(!store){
                return res.status(400).json({
                    message:"s_token"
                })
            }
            const commodity = await Storecommodity.findById(cid)
            if(!cid){
                return res.status(400).json({
                    message:""
                })
            }
            if(String(store._id)!==String(commodity.author)){
                return res.status(400).json({
                    message:""
                })
            }
            store.storecommodities.pull(commodity)
            await store.save()
            user.cinformations.pull(commodity)
            await user.save()
            await commodity.remove() //
            return res.json({
                message:""
            })

        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    })
    
}

export default storecommodityAPIs