import mongoose from 'mongoose'
import User from '../model/User'
import Secondhand from '../model/Secondhand'
import Comment4 from '../model/Comment4'
import File from '../model/File'
import Store from '../model/Store'
import multer from 'multer'

const upload = multer({//staticupload
    dest:'./static/upload'
})

const secondhandAPIs = (app)=>{
    
    //
    app.post('/api/secondhands',upload.array('pictures'),async (req,res)=>{//upload.array('pictures',9)
        try {
            const {username,token,category,price,title,content,number,filelist} = req.body
            console.log("fileList:",filelist)
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!category){
                return res.status(400).json({
                    message:""
                })
            }
            if(!price){
                return res.status(400).json({
                    message:""
                })
            }
            if(!title){
                return res.status(400).json({
                    message:""
                })
            }
            if(!content){
                return res.status(400).json({
                    message:""
                })
            }
            if(!number){
                return res.status(400).json({
                    message:""
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const fl = filelist.flat(Infinity);
            console.log(fl)

            const newSecondhand = {//
                category:category,
                price:price,
                title:title,
                content:content,
                number:number,
                author:user //
            }
            const secondhand = new Secondhand(newSecondhand)//
            secondhand.pictures = fl
            await secondhand.save()//saveawait
            user.secondhands.push(secondhand)
            await user.save()
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
        
    }),

    //
    app.post('/api/secondhands/:sid',upload.array('pictures',9),async (req,res)=>{//upload.array('picture',5)
        try {
            const {sid} = req.params
            if(!sid){
                return res.status(400).json({
                    message:"ID"
                })
            }
            const {username,token,storename,s_token,category,price,title,content,number} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!storename){
                return res.status(400).json({
                    message:""
                })
            }
            if(!s_token){
                return res.status(400).json({
                    message:"s_token"
                })
            }
            if(!category){
                return res.status(400).json({
                    message:""
                })
            }
            if(!price){
                return res.status(400).json({
                    message:""
                })
            }
            if(!title){
                return res.status(400).json({
                    message:""
                })
            }
            if(!content){
                return res.status(400).json({
                    message:""
                })
            }
            if(!number){
                return res.status(400).json({
                    message:""
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const store = await Store.findOne({
                storename:storename,
                s_token:s_token
            })
            if(!store){
                return res.status(400).json({
                    message:"s_token"
                })
            }
            const newSecondhand = {//
                category:category,
                price:price,
                title:title,
                content:content,
                number:number,
                author:user,//
                target:sid
            }
            const secondhand = new Secondhand(newSecondhand)//
            if(req.files){
                req.files.forEach(async (item,index)=>{
                    // console.log(item);
                    const file = new File({
                        filename:item.filename,
                        mimetype:item.mimetype,
                        size:item.size,
                        author:user
                        })
                    
                    secondhand.pictures.push(file)
                    await file.save()
                })
            }
            await secondhand.save()//saveawait
            user.secondhands.push(secondhand)//user
            await user.save()
            store.cinformations.push(secondhand)//store
            await store.save()
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),

    //
    app.get('/api/secondhands',async (req,res)=>{
        try {
            const secondhands = await Secondhand.find()
            .populate('author','username stores')//findone,populate
            .sort({"posttime":-1})
            return res.json({
                data:secondhands
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),
    //
    app.get('/api/secondhands/:sid',async (req,res)=>{
        try {
            const {sid} = req.params
            if(!sid){
                return res.status(400).json({
                    message:"ID"
                })
            }
            const secondhand = await Secondhand.findById(sid)
            .populate('author','username avatar description nickname')
            .populate('dianzans_secondhand')
            if(!secondhand){
                return res.status(400).json({
                    message:""
                })
            }
            const comments = await Comment4.find({target:sid}).populate('author','username avatar description nickname')//
            return res.json({
                data:{
                    secondhand,
                    comments
                }
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),
    //
    app.patch('/api/secondhands/:sid',upload.array('pictures',9),async (req,res)=>{
        try {
            const {sid} = req.params
            if(!sid){
                return res.status(400).json({
                    message:"ID"
                })
            }
            const {username,token,category,price,title,content,number} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!category){
                return res.status(400).json({
                    message:""
                })
            }
            if(!price){
                return res.status(400).json({
                    message:""
                })
            }
            if(!title){
                return res.status(400).json({
                    message:""
                })
            }
            if(!content){
                return res.status(400).json({
                    message:""
                })
            }
            if(!number){
                return res.status(400).json({
                    message:""
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const secondhand = await Secondhand.findById(sid)
            if(!sid){
                return res.status(400).json({
                    message:""
                })
            }
            if(String(user._id)!==String(secondhand.author)){
                return res.status(400).json({
                    message:""
                })
            }
            if(req.files){
                req.files.forEach(async (item,index)=>{
                    // console.log(item);
                    const file = new File({
                        filename:item.filename,
                        mimetype:item.mimetype,
                        size:item.size,
                        author:user
                        })
                    
                    secondhand.pictures.push(file)
                    await file.save()
                })
            }
            secondhand.category = category
            secondhand.price = price
            secondhand.title = title
            secondhand.content = content
            secondhand.number = number
            await secondhand.save()
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),
    //
    app.delete('/api/secondhands/:sid',async (req,res)=>{
        try {
           const {sid} = req.params
           if(!sid){
               return res.status(400).json({
                   message:"ID"
               })
           }
           const{username,token} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const secondhand = await Secondhand.findById(sid)
            if(!sid){
                return res.status(400).json({
                    message:""
                })
            }
            if(String(user._id)!==String(secondhand.author)){
                return res.status(400).json({
                    message:""
                })
            }
            user.secondhands.pull(secondhand)
            await user.save()
            await secondhand.remove() //
            return res.json({
                message:""
            })

        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),

    //
    app.delete('/api/stores/:sid',async (req,res)=>{
        try {
           const {sid} = req.params
           if(!sid){
               return res.status(400).json({
                   message:"ID"
               })
           }
           const{username,token,storename,s_token} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!storename){
                return res.status(400).json({
                    message:""
                })
            }
            if(!s_token){
                return res.status(400).json({
                    message:"s_token"
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const store = await Store.findOne({
                storename:storename,
                s_token:s_token
            })
            if(!store){
                return res.status(400).json({
                    message:"s_token"
                })
            }
            const secondhand = await Secondhand.findById(sid)
            if(!sid){
                return res.status(400).json({
                    message:""
                })
            }
            if(String(user._id)!==String(secondhand.author)){
                return res.status(400).json({
                    message:""
                })
            }
            await Secondhand.deleteMany({
                target:mongoose.Types.ObjectId(sid)
            })
            user.secondhands.pull(secondhand)
            await user.save()
            await secondhand.remove() //
            return res.json({
                message:""
            })

        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    })
    
}

export default secondhandAPIs