import mongoose from 'mongoose'

const StoreSchema = new mongoose.Schema({
    storename:{//
        type:String,
        unique:true,
        required:true,
        trim:true
    },
    password:{//
        type:String,
        required:true,
        trim:true
    },
    posttime:{//
        type:Date,
        default:Date.now()
    },
    avatar:String,//
    description:{//
        type:String,
        trim:true
    },
    s_token:String,//
    url:String,
    author:{
        type:mongoose.SchemaTypes.ObjectId,
        required:true,
        ref:'User'
    },
    storecommodities:[{//
        type:mongoose.SchemaTypes.ObjectId,
        ref:'Storecommodity'//
    }]
})

const Store = mongoose.model('Store',StoreSchema)
export default Store