import mongoose from 'mongoose'
import User from '../model/User'
import Unlikecomment from '../model/Unlikecomment'
import Unlikediscuss from '../model/Unlikediscuss'
import multer from 'multer'

const upload = multer({//staticupload
    dest:'./static/upload'
})

const unlikedisAPIs = (app)=>{

    //
    app.post('/api/unlikediscusses',upload.array('pictures'),async (req,res)=>{
        try {
            const {username,token,title,content,filelist} = req.body
            console.log("fileList:",filelist)
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!title){
                return res.status(400).json({
                    message:""
                })
            }
            if(!content){
                return res.status(400).json({
                    message:""
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            
            const fl = filelist.flat(Infinity);
            console.log(fl)

            const newDiscuss = {//
                title:title,
                content:content,
                author:user //
            }
            const unlikediscuss = new Unlikediscuss(newDiscuss)//mongoosenew()
            unlikediscuss.pictures = fl
            await unlikediscuss.save()//saveawait
            user.unlikediscusses.push(unlikediscuss)
            await user.save()
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({   
                message:e.message
            })
        }
    }),

    //()
    app.post('/api/unlikecomments/:uid',async (req,res)=>{//didID
        try {
            const {uid} = req.params
            if(!uid){
                return res.status(400).json({
                    message:"ID"
                })  
            }
            const {username,token,content} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!content){
                return res.status(400).json({
                    message:""
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const unlikediscuss = await Unlikediscuss.findById(uid)//findByIdID
            if(!unlikediscuss){
                return res.status(400).json({
                    message:""
                })
            }
            const newComment = {
                content:content,
                author:user,//
                target:uid//
            }
            const unlikecomment = new Unlikecomment(newComment)//mongoosenew
            await unlikecomment.save()//saveawait
            unlikediscuss.comments.push(unlikecomment)
            await unlikediscuss.save()
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({   
                message:e.message
            })
        }
    }),

    //
    app.get('/api/unlikediscusses',async (req,res)=>{
        try {
            const unlikediscusses = await Unlikediscuss.find().populate('author','username avatar nickname')//findone,populate
            return res.json({
                data:unlikediscusses
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),

    //
    app.get('/api/unlikediscusses/:uid',async (req,res)=>{
        try {
            const {uid} = req.params
            if(!uid){
                return res.status(400).json({
                    message:"ID"
                })  
            }
            const unlikediscuss = await Unlikediscuss.findById(uid)
            .populate('author','username avatar description nickname')//findByIdID
            .populate('dianzans_unlikediscuss')
            if(!unlikediscuss){
                return res.status(400).json({
                    message:""
                })
            }

            //targetdid
            const unlikecomments = await Unlikecomment.find({target:uid}).populate('author','username avatar description nickname')//
            return res.json({
                data:{//{}
                    unlikediscuss:unlikediscuss,
                    unlikecomments:unlikecomments
                }
            })  
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),

    
    
    //
    app.patch('/api/unlikediscusses/:uid',async (req,res)=>{
        try {
            const {uid} = req.params
            if(!uid){
                return res.status(400).json({
                    message:"ID"
                })  
            }
            const {username,token,title,content} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!title){
                return res.status(400).json({
                    message:""
                })
            }
            if(!content){
                return res.status(400).json({
                    message:""
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const unlikediscuss = await Unlikediscuss.findById(uid)
            if(!unlikediscuss){
                return res.status(400).json({
                    message:""
                })
            }
            if(String(user._id)!==String(unlikediscuss.author)){
                return res.status(400).json({
                    message:""
                })
            }
            unlikediscuss.title = title
            unlikediscuss.content = content
            await unlikediscuss.save()
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({   
                message:e.message
            })
        }
    }),

    //
    app.delete('/api/unlikediscusses/:uid',async (req,res)=>{
        try {
            const {uid} = req.params
            if(!uid){
                return res.status(400).json({
                    message:"ID"
                })
            }
            const{username,token} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const unlikediscuss = await Unlikediscuss.findById(uid)
            if(!unlikediscuss){
                return res.status(400).json({
                    message:""
                })
            }
            if(String(user._id)!==String(unlikediscuss.author)){
                return res.status(400).json({
                    message:""
                })
            }
            await Unlikecomment.deleteMany({
                target:mongoose.Types.ObjectId(uid)
            })
            user.unlikediscusses.pull(unlikediscuss)
            await user.save()
            await unlikediscuss.remove() //
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),

    //
    app.delete('/api/unlikecomments/:uid',async (req,res)=>{
        try {
            const{uid} = req.params
            if(!uid){
                return res.status(400).json({
                    message:"ID"
                })
            }
            const{username,token} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const unlikecomment = await Unlikecomment.findById(uid)
            if(!unlikecomment){
                return res.status(400).json({
                    message:""
                })
            }
            if(String(user._id)!==String(unlikecomment.author)){
                return res.status(400).json({
                    message:""
                })
            }
            await unlikecomment.remove() //
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    })
    
}

export default unlikedisAPIs