import mongoose from 'mongoose'
import User from './User'
import Store from './Store'
import Comment2 from './Comment2'
import Shoppingcart from './Shoppingcart'

const CommoditySchema = new mongoose.Schema({
    category:{//
        type:String,
        required:true,
        trim:true
    },
    price:{//
        type:Number,
        required:true
    },
    title:{//
        type:String,
        required:true,
        trim:true
    },
    content:{//
        type:String,
        required:true,
        trim:true
    },
    posttime:{//
        type:Date,
        default:Date.now()
    },
    pictures:[{//
        type:String
    }],
    url:String,//
    author:{
        type:mongoose.SchemaTypes.ObjectId,
        required:true,
        ref:'User'
    },
    // target:{//id
    //     type:mongoose.SchemaTypes.ObjectId,
    //     ref:'Store'
    // },
    comments:[{//
        type:mongoose.SchemaTypes.ObjectId,
        ref:'Comment2'//
    }],
    dianzans_commodity:[{//
        type:mongoose.SchemaTypes.ObjectId,
        ref:'User'//
    }],

})

const Commodity = mongoose.model('Commodity',CommoditySchema)
export default Commodity