import React,{useState,useEffect} from 'react'//useStatereact
import {Form,Button} from 'react-bootstrap'
import {HOST,PORT,DOMAIN,DOMAIN2} from '../../config'

const SettingStoreForm = (props)=>{
    const {sid} = props
    const [settings,setSettings] = useState([])//useStatesettings[]
    const loadSettings = async ()=>{//
        try {
            const storage = await localStorage.getItem(DOMAIN)//storagegetItem,DOMAIN
            const {username,token} = JSON.parse(storage)//storagejson
            const storage2 = await localStorage.getItem(DOMAIN2)//storagegetItem,DOMAIN
            const {storename,s_token} = JSON.parse(storage2)//storagejson
            const res = await fetch(`${HOST}:${PORT}/api/stores/${sid}`,{//
                method:"GET"
            })
            const result = await res.json()//json
            if(res.ok){
                setSettings(result.data)//setSettingssettings
            }else{
                alert(result.message)
            }
        } catch (error) {
            alert(error.message)
        }
    }

    useEffect(()=>{//
        loadSettings()
    },[])//[]

    const setting = async (body)=>{//
        try {
            const storage = await localStorage.getItem(DOMAIN)//tokenstorage
            const {username,token} = JSON.parse(storage)//storagejson
            const storage2 = await localStorage.getItem(DOMAIN2)//tokenstorage
            const {storename,s_token} = JSON.parse(storage2)//storagejson
            const {description} = body
            const formData = new FormData()//formData
            await formData.append('username',username)
            await formData.append('token',token)
            await formData.append('storename',storename)
            await formData.append('s_token',s_token)
            await formData.append('description',description)
            await formData.append('avatar',document.querySelector('#uploader').files[0])//files[0]
            const res = await fetch(`${HOST}:${PORT}/api/stores`,{
                method:"PATCH",
                body:formData
            })
            const result = await res.json()
            if(res.ok){
                alert(result.message)
                loadSettings()//loadSettings
            }else{
                alert(result.message)
            }
        } catch (error) {
            alert(error.message)
        }
    }

    const handleSettings = (e)=>{
        e.preventDefault()//
        const form = document.forms.settingstoreForm
        const description = form.description.value
        const body = {description}
        setting(body)//registerbody
    }
    return(
        //file
        <Form id="settingstoreForm">
            <Form.Group controlId="description">
                <Form.Label></Form.Label>
                <Form.Control type="text" placeholder="" defaultValue={settings.description}/>
            </Form.Group>
            <Form.Group controlId="uploader">
                <Form.Label></Form.Label>
                <img
                    width={64}
                    height={64}
                    className="mr-3 img-thumbnail round"
                    src={settings.avatar ? `/upload/${settings.avatar}` : '/img/avatar.jpg'}
                    alt=""
                />
                <Form.Control type="file" placeholder=""/>
            </Form.Group>
            <Button variant="primary" type="submit" onClick={(e)=>handleSettings(e)}></Button>
        </Form>
    )
}

export default SettingStoreForm