import React,{useState,useContext} from 'react'
import {Form,Button,Modal} from 'react-bootstrap'
import {HOST,PORT,DOMAIN} from '../config'
import RegisterButton from './RegisterButton'
import {userContext} from './App'

const LoginForm = ()=>{
    return (
        <Form id="loginForm">
            <Form.Group controlId="username">
                <Form.Label></Form.Label>
                <Form.Control type="text" placeholder=""/>
            </Form.Group>
            <Form.Group controlId="password">
                <Form.Label></Form.Label>
                <Form.Control type="password" placeholder=""/>
            </Form.Group>
            <RegisterButton/>
        </Form>
    )
}

const LoginButton = ()=>{
    const {setUser,setAuth} = useContext(userContext)
    const [show,setShow] = useState(false)//
    const showModal = ()=> setShow(true)//
    const closeModal = ()=> setShow(false)//
    const login = async (body)=>{
        try {
            const res = await fetch(`${HOST}:${PORT}/api/users/login`,{ 
                method:"POST",
                headers:{"Content-Type":"application/json"},
                body:JSON.stringify(body)
            })
            const result = await res.json()
            if(res.ok){
                alert(result.message)
                closeModal()//
                const data = {
                    username:result.data.username,
                    token:result.data.token
                }//token
                await localStorage.setItem(DOMAIN,JSON.stringify(data))//datasetItem(a,b)ab
                setAuth(true)
                setUser({
                    _id:result.data._id,
                    username:result.data.username,
                    avatar:result.data.avatar
                })
            }else{
                alert(result.message)
            }
        } catch (error) {
            alert(error.message)
        }
        
    }
    const handleLogin = (e)=>{
        e.preventDefault()//
        const form = document.forms.loginForm
        const username = form.username.value //value
        const password = form.password.value
        const body = {username,password}//bodybody
        login(body)
    }
    return(
        <div>
            <Button variant="outline-dark" size="sm" onClick={()=>showModal()}>
                
            </Button>
            <Modal show={show} onHide={()=>closeModal()}>
                <Modal.Header closeButton>
                    <Modal.Title></Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <LoginForm/>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={()=>closeModal()}></Button>
                    <Button variant="primary" onClick={(e)=>handleLogin(e)}></Button>
                </Modal.Footer>
            </Modal>
        </div>
    )
}

export default LoginButton