import mongoose from 'mongoose'
import User from '../model/User'
import Helpcomment from '../model/Helpcomment'
import Helpdiscuss from '../model/Helpdiscuss'
import multer from 'multer'

const upload = multer({//staticupload
    dest:'./static/upload'
})

const HelpdisAPIs = (app)=>{

    //
    app.post('/api/helpdiscusses',upload.array('pictures'),async (req,res)=>{
        try {
            const {username,token,title,content,filelist} = req.body
            console.log("fileList:",filelist)
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!title){
                return res.status(400).json({
                    message:""
                })
            }
            if(!content){
                return res.status(400).json({
                    message:""
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }

            const fl = filelist.flat(Infinity);
            console.log(fl)

            const newDiscuss = {//
                title:title,
                content:content,
                author:user //
            }
            const helpdiscuss = new Helpdiscuss(newDiscuss)//mongoosenew()
            helpdiscuss.pictures = fl
            await helpdiscuss.save()//saveawait
            user.helpdiscusses.push(helpdiscuss)
            await user.save()
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({   
                message:e.message
            })
        }
    }),


    //()
    app.post('/api/helpcomments/:hid',async (req,res)=>{//didID
        try {
            const {hid} = req.params
            if(!hid){
                return res.status(400).json({
                    message:"ID"
                })  
            }
            const {username,token,content} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!content){
                return res.status(400).json({
                    message:""
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const helpdiscuss = await Helpdiscuss.findById(hid)//findByIdID
            if(!helpdiscuss){
                return res.status(400).json({
                    message:""
                })
            }
            const newComment = {
                content:content,
                author:user,//
                target:hid//
            }
            const helpcomment = new Helpcomment(newComment)//mongoosenew
            await helpcomment.save()//saveawait
            helpdiscuss.comments.push(helpcomment)
            await helpdiscuss.save()
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({   
                message:e.message
            })
        }
    }),

    //
    app.get('/api/helpdiscusses',async (req,res)=>{
        try {
            const helpdiscusses = await Helpdiscuss.find().populate('author','username avatar nickname')//findone,populate
            return res.json({
                data:helpdiscusses
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),

    //
    app.get('/api/helpdiscusses/:hid',async (req,res)=>{
        try {
            const {hid} = req.params
            if(!hid){
                return res.status(400).json({
                    message:"ID"
                })  
            }
            const helpdiscuss = await Helpdiscuss.findById(hid)
            .populate('author','username avatar description nickname')//findByIdID
            .populate('dianzans_helpdiscuss')
            if(!helpdiscuss){
                return res.status(400).json({
                    message:""
                })
            }

            //targetdid
            const helpcomments = await Helpcomment.find({target:hid}).populate('author','username avatar description nickname')//
            return res.json({
                data:{//{}
                    helpdiscuss:helpdiscuss,
                    helpcomments:helpcomments
                }
            })  
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),

    
    
    //
    app.patch('/api/helpdiscusses/:hid',async (req,res)=>{
        try {
            const {hid} = req.params
            if(!hid){
                return res.status(400).json({
                    message:"ID"
                })  
            }
            const {username,token,title,content} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!title){
                return res.status(400).json({
                    message:""
                })
            }
            if(!content){
                return res.status(400).json({
                    message:""
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const helpdiscuss = await Helpdiscuss.findById(hid)
            if(!helpdiscuss){
                return res.status(400).json({
                    message:""
                })
            }
            if(String(user._id)!==String(helpdiscuss.author)){
                return res.status(400).json({
                    message:""
                })
            }
            helpdiscuss.title = title
            helpdiscuss.content = content
            await helpdiscuss.save()
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({   
                message:e.message
            })
        }
    }),

    //
    app.delete('/api/helpdiscusses/:hid',async (req,res)=>{
        try {
            const {hid} = req.params
            if(!hid){
                return res.status(400).json({
                    message:"ID"
                })
            }
            const{username,token} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const helpdiscuss = await Helpdiscuss.findById(hid)
            if(!helpdiscuss){
                return res.status(400).json({
                    message:""
                })
            }
            if(String(user._id)!==String(helpdiscuss.author)){
                return res.status(400).json({
                    message:""
                })
            }
            await Helpcomment.deleteMany({
                target:mongoose.Types.ObjectId(hid)
            })
            user.helpdiscusses.pull(helpdiscuss)
            await user.save()
            await helpdiscuss.remove() //
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),

    //
    app.delete('/api/helpcomments/:hid',async (req,res)=>{
        try {
            const{hid} = req.params
            if(!hid){
                return res.status(400).json({
                    message:"ID"
                })
            }
            const{username,token} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const helpcomment = await Helpcomment.findById(hid)
            if(!helpcomment){
                return res.status(400).json({
                    message:""
                })
            }
            if(String(user._id)!==String(helpcomment.author)){
                return res.status(400).json({
                    message:""
                })
            }
            await helpcomment.remove() //
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    })
    
}

export default HelpdisAPIs