import mongoose from 'mongoose'
import User from '../model/User'
import Commodity from '../model/Commodity'
import Comment2 from '../model/Comment2'
import File from '../model/File'
import Store from '../model/Store'
import images from 'images'
import fs from 'fs'
import multer from 'multer'

const upload = multer({//staticupload
    dest:'./static/upload'
})

const commodityAPIs = (app)=>{


     // 
     app.post("/api/upload",upload.array('pictures'),(req,res)=>{
        try {  
        console.log("uploadreq.files:",req.files)
        // req.files.forEach(async f=>{
        //     const path = `${f.destination}/${f.filename}`
        //     const newPath = `${f.destination}/tmp/${f.filename}`
        //     console.log("path:",path)
        //     await images(path).size(48,48).save(newPath);
        // })      
        const filelist = req.files.map(f=>f.filename)
        return res.json({
            message:"",
            data:filelist,

        })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    })

    //
    app.post('/api/commodities',upload.array('pictures'),async (req,res)=>{
        try {
            const {username,token,category,price,title,content,filelist} = req.body
            console.log("fileList:",filelist)
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!category){
                return res.status(400).json({
                    message:""
                })
            }
            if(!price){
                return res.status(400).json({
                    message:""
                })
            }
            if(!title){
                return res.status(400).json({
                    message:""
                })
            }
            if(!content){
                return res.status(400).json({
                    message:""
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const fl = filelist.flat(Infinity);
            console.log(fl)
           
            const newCommodity = {//
                category:category,
                price:price,
                title:title,
                content:content,
                author:user //
            }
            const commodity = new Commodity(newCommodity)//
            commodity.pictures = fl
            await commodity.save()//saveawait
            user.mcommodities.push(commodity)
            await user.save()
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
        
    }),

    //
    app.get('/api/commodities',async (req,res)=>{
        try {
            const commodities = await Commodity.find()
            .populate('author','username stores nickname')//findone,populate
            .sort({"posttime":-1})
            return res.json({
                data:commodities
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),

   
    //
    app.get('/api/commodities/:cid',async (req,res)=>{
        try {
            const {cid} = req.params
            if(!cid){
                return res.status(400).json({
                    message:"ID"
                })
            }
            const commodity = await Commodity.findById(cid)
            .populate('author','username avatar description nickname')
            .populate('dianzans_commodity')
            if(!commodity){
                return res.status(400).json({
                    message:""
                })
            }
            const comments = await Comment2.find({target:cid}).populate('author','username avatar description nickname')//
            return res.json({
                data:{
                    commodity,
                    comments
                }
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),
    //
    app.patch('/api/commodities/:cid',upload.array('pictures',9),async (req,res)=>{
        try {
            const {cid} = req.params
            if(!cid){
                return res.status(400).json({
                    message:"ID"
                })
            }
            const {username,token,category,price,title,content} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!category){
                return res.status(400).json({
                    message:""
                })
            }
            if(!price){
                return res.status(400).json({
                    message:""
                })
            }
            if(!title){
                return res.status(400).json({
                    message:""
                })
            }
            if(!content){
                return res.status(400).json({
                    message:""
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            
            const commodity = await Commodity.findById(cid)
            if(!cid){
                return res.status(400).json({
                    message:""
                })
            }
            if(String(user._id)!==String(commodity.author)){
                return res.status(400).json({
                    message:""
                })
            }
            
            commodity.category = category
            commodity.price = price
            commodity.title = title
            commodity.content = content
            await commodity.save()
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),
    //
    app.delete('/api/commodities/:cid',async (req,res)=>{
        try {
           const {cid} = req.params
           if(!cid){
               return res.status(400).json({
                   message:"ID"
               })
           }
           const{username,token} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const commodity = await Commodity.findById(cid)
            if(!cid){
                return res.status(400).json({
                    message:""
                })
            }
            if(String(user._id)!==String(commodity.author)){
                return res.status(400).json({
                    message:""
                })
            }
            // await Discuss.deleteMany({
            //     target:mongoose.Types.ObjectId(cid)
            // })

            user.mcommodities.pull(commodity)
            await user.save()
            await commodity.remove() //
            return res.json({
                message:""
            })

        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    })

}

export default commodityAPIs