import mongoose from 'mongoose'
import User from './User'

const CommentSchema = new mongoose.Schema({
    content:{//
        type:String,
        required:true,
        trim:true
    },
    posttime:{//
        type:Date,
        default:Date.now()
    },
    anonymous:Boolean,//
    author:{//
        type:mongoose.SchemaTypes.ObjectId,
        requried:true,
        ref:'User'
    },
    target:{//id
        type:mongoose.SchemaTypes.ObjectId,
        requried:true,
        ref:'Groupbuy'
    }
})

const Comment3 = mongoose.model('Comment3',CommentSchema)
export default Comment3