import mongoose from 'mongoose'
import User from './User'
import Commodity from './Commodity'

const UnlikediscussSchema = new mongoose.Schema({
    title:{//
        type:String,
        required:true,
        trim:true
    },
    content:{//
        type:String,
        required:true,
        trim:true
    },
    posttime:{//
        type:Date,
        default:Date.now()
    },
    pictures:[{//
        type:String
    }],
    anonymous:Boolean,//
    author:{//
        type:mongoose.SchemaTypes.ObjectId,
        required:true,
        ref:'User'
    },
    comments:[{
        type:mongoose.SchemaTypes.ObjectId,
        ref:'Unlikecomment'
    }],
    dianzans_unlikediscuss:[{//
        type:mongoose.SchemaTypes.ObjectId,
        ref:'User'//
    }]
})

const Unlikediscuss = mongoose.model('Unlikediscuss',UnlikediscussSchema)
export default Unlikediscuss