import mongoose from 'mongoose'
import User from './User'
import Store from './Store'
import Comment2 from './Comment2'

const GroupbuySchema = new mongoose.Schema({
    category:{//
        type:String,
        required:true,
        trim:true
    },
    price:{//
        type:Number,
        required:true
    },
    motoprice:{//
        type:Number,
        required:true
    },
    title:{//
        type:String,
        required:true,
        trim:true
    },
    content:{//
        type:String,
        required:true,
        trim:true
    },
    // number:{
    //     type:Number,
    //     required:true
    // },
    posttime:{//
        type:Date,
        default:Date.now()
    },
    pictures:[{//
        type:String
    }],
    url:String,//
    author:{
        type:mongoose.SchemaTypes.ObjectId,
        required:true,
        ref:'User'
    },
    target:{//id
        type:mongoose.SchemaTypes.ObjectId,
        requried:true,
        ref:'Store'
    },
    comments:[{//
        type:mongoose.SchemaTypes.ObjectId,
        ref:'Comment3'//
    }],
    dianzans_groupbuy:[{//
        type:mongoose.SchemaTypes.ObjectId,
        ref:'User'//
    }],
})

const Groupbuy = mongoose.model('Groupbuy',GroupbuySchema)
export default Groupbuy