import mongoose from 'mongoose'
import User from '../model/User'
import Pcomment from '../model/Pcomment'
import Pletter from '../model/Pletter'

const plettersAPIs = (app)=>{

    //
    app.post('/api/pletters/:uid',async (req,res)=>{
        try {
            const {uid} = req.params
            if(!uid){
                return res.status(400).json({
                    message:"ID"
                })  
            }
            const {username,token,content} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!content){
                return res.status(400).json({
                    message:""
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const target_user = await User.findById(uid)
            if(!target_user){
                return res.status(400).json({
                    message:""
                })
            }
            if(String(target_user._id)===String(user._id)){
                return res.status(400).json({
                    message:""
                })
            }
            const newLetter = {//
                content:content,
                author:user, //
                target:uid
            }
            const pletter = new Pletter(newLetter)//mongoosenew()
            await pletter.save()//saveawait
            user.pletters.push(pletter)
            await user.save()
            target_user.pletters.push(pletter)
            await target_user.save()
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({   
                message:e.message
            })
        }
    }),

    //
    app.post('/api/pcomments/:pid',async (req,res)=>{//didID
        try {
            const {pid} = req.params
            if(!pid){
                return res.status(400).json({
                    message:"ID"
                })  
            }
            const {username,token,content} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!content){
                return res.status(400).json({
                    message:""
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const pletter = await Pletter.findById(pid)//findByIdID
            if(!pletter){
                return res.status(400).json({
                    message:""
                })
            }
            const newComment = {
                content:content,
                author:user,//
                target:pid//
            }
            const pcomment = new Pcomment(newComment)//mongoosenew
            await pcomment.save()//saveawait
            pletter.pcomments.push(pcomment)
            await pletter.save()
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({   
                message:e.message
            })
        }
    }),

    //()
    app.get('/api/pletters/fromsb/:uid',async (req,res)=>{
        try {
            const {uid} = req.params
            if(!uid){
                return res.status(400).json({
                    message:"ID"
                })  
            }
            const user = await User.findById(uid)
            if(!user){
                return res.status(400).json({
                    message:""
                })
            }
            const pletters = await Pletter.find({target:uid})
            .populate('author','username avatar')//findone,populate
            .populate('target','username avatar')//findone,populate
            .sort({"posttime":-1})
            return res.json({
                data:pletters
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),

    //()
    app.get('/api/pletters/tosb/:uid',async (req,res)=>{
        try {
            const {uid} = req.params
            if(!uid){
                return res.status(400).json({
                    message:"ID"
                })  
            }
            const user = await User.findById(uid)
            if(!user){
                return res.status(400).json({
                    message:""
                })
            }
            const pletters = await Pletter.find({author:uid})
            .populate('author','username avatar')//findone,populate
            .populate('target','username avatar')//findone,populate
            .sort({"posttime":-1})
            return res.json({
                data:pletters
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),
    //
    app.get('/api/pletters/:pid',async (req,res)=>{
        try {
            const {pid} = req.params
            if(!pid){
                return res.status(400).json({
                    message:"ID"
                })  
            }
            const pletter = await Pletter.findById(pid).populate('author','username avatar description nickname')//findByIdID
            if(!pletter){
                return res.status(400).json({
                    message:""
                })
            }

            //targetuid
            const pcomments = await Pcomment.find({target:pid}).populate('author','username avatar description nickname')//
            return res.json({
                data:{//{}
                    pletter:pletter,
                    pcomments:pcomments
                }
            })  
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),

    //
    app.delete('/api/pletters/:pid',async (req,res)=>{
        try {
            const {pid} = req.params
            if(!pid){
                return res.status(400).json({
                    message:"ID"
                })
            }
            const{username,token} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const pletter = await Pletter.findById(pid)
            if(!pletter){
                return res.status(400).json({
                    message:""
                })
            }
            if(String(user._id)!==String(pletter.author)){
                return res.status(400).json({
                    message:""
                })
            }
            await Pcomment.deleteMany({
                target:mongoose.Types.ObjectId(pid)
            })
            user.pletters.pull(pletter)
            await user.save()
            await pletter.remove() //
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),

    //
    app.delete('/api/pcomments/:pid',async (req,res)=>{
        try {
            const{pid} = req.params
            if(!pid){
                return res.status(400).json({
                    message:"ID"
                })
            }
            const{username,token} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const pcomment = await Pcomment.findById(pid)
            if(!pcomment){
                return res.status(400).json({
                    message:""
                })
            }
            if(String(user._id)!==String(pcomment.author)){
                return res.status(400).json({
                    message:""
                })
            }
            await pcomment.remove() //
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    })
    
}

export default plettersAPIs