import React,{useState,useContext} from 'react'
import {Form,Button,Modal} from 'react-bootstrap'
import {HOST,PORT,DOMAIN,DOMAIN2} from '../config'
import StoreButton from './StoreButton'
import {userContext} from './App' 

const LoginStoreForm = ()=>{
    return (
        <Form id="loginstoreForm">
            <Form.Group controlId="storename">
                <Form.Label></Form.Label>
                <Form.Control type="text" placeholder=""/>
            </Form.Group>
            <Form.Group controlId="password">
                <Form.Label></Form.Label>
                <Form.Control type="password" placeholder=""/>
            </Form.Group>
            <StoreButton/>
        </Form>
    )
}

const LoginStoreButton = ()=>{
    const {setStore,setAuth2} = useContext(userContext)
    const [show,setShow] = useState(false)//
    const showModal = ()=> setShow(true)//
    const closeModal = ()=> setShow(false)//
    const login = async (body)=>{
        try {
            const res = await fetch(`${HOST}:${PORT}/api/stores/login`,{ 
                method:"POST",
                headers:{"Content-Type":"application/json"},
                body:JSON.stringify(body)
            })
            const result = await res.json()
            if(res.ok){
                alert(result.message)
                closeModal()//
                const data = {
                    storename:result.data.storename,
                    s_token:result.data.s_token
                }//token
                await localStorage.setItem(DOMAIN2,JSON.stringify(data))//datasetItem(a,b)ab
                setAuth2(true)
                setStore({
                    _id:result.data._id,
                    storename:result.data.storename,
                    avatar:result.data.avatar
                })
            }else{
                alert(result.message)
            }
        } catch (error) {
            alert(error.message)
        }
        
    }
    const handleLogin = (e)=>{
        e.preventDefault()//
        const storage =  localStorage.getItem(DOMAIN)//storage
        const {username,token} = JSON.parse(storage)//storagejson
        const form = document.forms.loginstoreForm
        const storename = form.storename.value //value
        const password = form.password.value
        const body = {username,token,storename,password}//bodybody
        login(body)
    }
    return(
        <div>
            <Button variant="outline-dark" size="sm" onClick={()=>showModal()}>
                
            </Button>
            <Modal show={show} onHide={()=>closeModal()}>
                <Modal.Header closeButton>
                    <Modal.Title></Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <LoginStoreForm/>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={()=>closeModal()}></Button>
                    <Button variant="primary" onClick={(e)=>handleLogin(e)}></Button>
                </Modal.Footer>
            </Modal>
        </div>
    )
}

export default LoginStoreButton