import mongoose from 'mongoose'
import User from '../model/User'
import Likecomment from '../model/Likecomment'
import Likediscuss from '../model/Likediscuss'
import multer from 'multer'

const upload = multer({//staticupload
    dest:'./static/upload'
})

const likedisAPIs = (app)=>{

    //
    app.post('/api/likediscusses',upload.array('pictures'),async (req,res)=>{
        try {
            const {username,token,title,content,filelist} = req.body
            console.log("fileList:",filelist)
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!title){
                return res.status(400).json({
                    message:""
                })
            }
            if(!content){
                return res.status(400).json({
                    message:""
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }

            const fl = filelist.flat(Infinity);
            console.log(fl)

            const newDiscuss = {//
                title:title,
                content:content,
                author:user //
            }
            const likediscuss = new Likediscuss(newDiscuss)//mongoosenew()
            likediscuss.pictures = fl
            await likediscuss.save()//saveawait
            user.likediscusses.push(likediscuss)
            await user.save()
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({   
                message:e.message
            })
        }
    }),

    //()
    app.post('/api/likecomments/:lid',async (req,res)=>{//didID
        try {
            const {lid} = req.params
            if(!lid){
                return res.status(400).json({
                    message:"ID"
                })  
            }
            const {username,token,content} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!content){
                return res.status(400).json({
                    message:""
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const likediscuss = await Likediscuss.findById(lid)//findByIdID
            if(!likediscuss){
                return res.status(400).json({
                    message:""
                })
            }
            const newComment = {
                content:content,
                author:user,//
                target:lid//
            }
            const likecomment = new Likecomment(newComment)//mongoosenew
            await likecomment.save()//saveawait
            likediscuss.comments.push(likecomment)
            await likediscuss.save()
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({   
                message:e.message
            })
        }
    }),

    //
    app.get('/api/likediscusses',async (req,res)=>{
        try {
            const likediscusses = await Likediscuss.find().populate('author','username avatar nickname')//findone,populate
            return res.json({
                data:likediscusses
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),

    //
    app.get('/api/likediscusses/:lid',async (req,res)=>{
        try {
            const {lid} = req.params
            if(!lid){
                return res.status(400).json({
                    message:"ID"
                })  
            }
            const likediscuss = await Likediscuss.findById(lid)
            .populate('author','username avatar description nickname')//findByIdID
            .populate('dianzans_likediscuss')
            if(!likediscuss){
                return res.status(400).json({
                    message:""
                })
            }

            //targetdid
            const likecomments = await Likecomment.find({target:lid}).populate('author','username avatar description nickname')//
            return res.json({
                data:{//{}
                    likediscuss:likediscuss,
                    likecomments:likecomments
                }
            })  
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),

    
    
    //
    app.patch('/api/likediscusses/:lid',async (req,res)=>{
        try {
            const {lid} = req.params
            if(!lid){
                return res.status(400).json({
                    message:"ID"
                })  
            }
            const {username,token,title,content} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!title){
                return res.status(400).json({
                    message:""
                })
            }
            if(!content){
                return res.status(400).json({
                    message:""
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const likediscuss = await Likediscuss.findById(lid)
            if(!likediscuss){
                return res.status(400).json({
                    message:""
                })
            }
            if(String(user._id)!==String(likediscuss.author)){
                return res.status(400).json({
                    message:""
                })
            }
            likediscuss.title = title
            likediscuss.content = content
            await likediscuss.save()
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({   
                message:e.message
            })
        }
    }),

    //
    app.delete('/api/likediscusses/:lid',async (req,res)=>{
        try {
            const {lid} = req.params
            if(!lid){
                return res.status(400).json({
                    message:"ID"
                })
            }
            const{username,token} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const likediscuss = await Likediscuss.findById(lid)
            if(!likediscuss){
                return res.status(400).json({
                    message:""
                })
            }
            if(String(user._id)!==String(likediscuss.author)){
                return res.status(400).json({
                    message:""
                })
            }
            await Likecomment.deleteMany({
                target:mongoose.Types.ObjectId(lid)
            })
            user.likediscusses.pull(likediscuss)
            await user.save()
            await likediscuss.remove() //
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),

    //
    app.delete('/api/likecomments/:lid',async (req,res)=>{
        try {
            const{lid} = req.params
            if(!lid){
                return res.status(400).json({
                    message:"ID"
                })
            }
            const{username,token} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const likecomment = await Likecomment.findById(lid)
            if(!likecomment){
                return res.status(400).json({
                    message:""
                })
            }
            if(String(user._id)!==String(likecomment.author)){
                return res.status(400).json({
                    message:""
                })
            }
            await likecomment.remove() //
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    })
    
}

export default likedisAPIs