import mongoose from 'mongoose'

const StorecommoditySchema = new mongoose.Schema({
    category:{//
        type:String,
        required:true,
        trim:true
    },
    price:{//
        type:Number,
        required:true
    },
    title:{//
        type:String,
        required:true,
        trim:true
    },
    content:{//
        type:String,
        required:true,
        trim:true
    },
    posttime:{//
        type:Date,
        default:Date.now()
    },
    pictures:[{//
        type:String
    }],
    author:{
        type:mongoose.SchemaTypes.ObjectId,
        required:true,
        ref:'Store'
    },
    comments:[{//
        type:mongoose.SchemaTypes.ObjectId,
        ref:'Commentstore'//
    }],
    dianzans_storecommodity:[{//
        type:mongoose.SchemaTypes.ObjectId,
        ref:'User'//
    }],

})

const Storecommodity = mongoose.model('Storecommodity',StorecommoditySchema)
export default Storecommodity