import mongoose from 'mongoose'

const PcommentSchema = new mongoose.Schema({
    content:{//
        type:String,
        required:true,
        trim:true
    },
    posttime:{//
        type:Date,
        default:Date.now()
    },
    author:{//
        type:mongoose.SchemaTypes.ObjectId,
        required:true,
        ref:'User'
    },
    target:{//id
        type:mongoose.SchemaTypes.ObjectId,
        requried:true,
        ref:'Pletter'
    }
})

const Pcomment = mongoose.model('Pcomment',PcommentSchema)
export default Pcomment