import mongoose from 'mongoose'
import User from './User'

const CommentSchema = new mongoose.Schema({
    content:{//
        type:String,
        required:true,
        trim:true
    },
    posttime:{//
        type:Date,
        default:Date.now()
    },
    anonymous:Boolean,//
    author:{//
        type:mongoose.SchemaTypes.ObjectId,
        requried:true,
        ref:'User'
    },
    target:{//id
        type:mongoose.SchemaTypes.ObjectId,
        requried:true,
        ref:'Secondhand'
    }
})

const Comment4 = mongoose.model('Comment4',CommentSchema)
export default Comment4