import mongoose from 'mongoose'
import Store from '../model/Store'
import User from '../model/User'
import Commodity from '../model/Commodity'
import Crypto from 'crypto'
import {v4 as uuidv4} from 'uuid'
import multer from 'multer'

const upload = multer({//staticupload
    dest:'./static/upload'
})

const storeAPIs = (app)=>{
    //
    app.post('/api/stores',async (req,res)=>{
        try {
            const {username,token,storename,password,confirmpass} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!storename){
                return res.status(400).json({
                    message:""
                })
            }
            if(!password){
                return res.status(400).json({
                    message:""
                })
            }
            if(!confirmpass){
                return res.status(400).json({
                    message:""
                })
            }
            if(password!==confirmpass){
                return res.status(400).json({
                    message:""
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const store = await Store.findOne({storename:storename})
            if(store){
                return res.status(400).json({
                    message:""
                })
            }
            if(user.stores.length >= 1){
                return res.status(400).json({
                    message:","
                })
            }
            console.log(user.stores.length)
            const passwordCryp = Crypto.createHash('sha1').update(password).digest('hex')
            const newStore = {//
                storename:storename,
                password:passwordCryp,
                author:user//
            }
            const newstore = new Store(newStore)//mongoosenew
            user.stores.push(newstore)//user
            await user.save()
            await newstore.save()//saveawait
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),
    //
    app.post('/api/stores/login',async (req,res)=>{
        try {
            const {username,token,storename,password} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!storename){
                return res.status(400).json({
                    message:""
                })
            }
            if(!password){
                return res.status(400).json({
                    message:""
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const passwordCryp = Crypto.createHash('sha1').update(password).digest('hex')
            const store = await Store.findOne({storename:storename})
            if(!store){
                return res.status(400).json({
                    message:""
                })
            }
            if(store.password!==passwordCryp){
                return res.status(400).json({
                    message:""
                })
            }
            if(String(user._id)!==String(store.author)){
                return res.status(400).json({
                    message:""
                })
            }
            const tokenstr = uuidv4();//
            const s_token = Crypto.createHash('sha1').update(tokenstr).digest('hex')
            store.s_token = s_token
            await store.save()
            return res.json({
                message:"",
                data:{//token
                    _id:store._id,
                    storename:store.storename,
                    s_token:store.s_token,
                    avatar:store.avatar
                }
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),

     //
     app.post('/api/stores/auth',async (req,res)=>{
        try {
            const {storename,s_token} = req.body
            // if(!username){
            //     return res.status(400).json({
            //         message:""
            //     })
            // }
            // if(!token){
            //     return res.status(400).json({
            //         message:"token"
            //     })
            // }
            if(!storename){
                return res.status(400).json({
                    message:""
                })
            }
            if(!s_token){
                return res.status(400).json({
                    message:"s_token"
                })
            }
            // const user = await User.findOne({
            //     username:username,
            //     token:token
            // })
            // if(!user){
            //     return res.status(400).json({
            //         message:"token"
            //     })
            // }
            const store = await Store.findOne({
                storename:storename,
                s_token:s_token
            })
            if(!store){
                return res.status(400).json({
                    message:"s_token"
                })
            }
            //token
            store.password = null
            store.s_token = null
            return res.json({
                message:"",
                data:store
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),
    
    //
    app.post('/api/stores/logout',async (req,res)=>{
        try {
            const {storename,s_token} = req.body
            // if(!username){
            //     return res.status(400).json({
            //         message:""
            //     })
            // }
            // if(!token){
            //     return res.status(400).json({
            //         message:"token"
            //     })
            // }
            if(!storename){
                return res.status(400).json({
                    message:""
                })
            }
            if(!s_token){
                return res.status(400).json({
                    message:"s_token"
                })
            }
            // const user = await User.findOne({
            //     username:username,
            //     token:token
            // })
            // if(!user){
            //     return res.status(400).json({
            //         message:"token"
            //     })
            // }
            const store = await Store.findOne({
                storename:storename,
                s_token:s_token
            })
            if(!store){
                return res.status(400).json({
                    message:"s_token"
                })
            }
            store.s_token = null//s_token
            await store.save()
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),
    // 
    app.patch('/api/stores',upload.single('avatar'),async (req,res)=>{
        try {
            const {username,token,storename,s_token,description} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            if(!storename){
                return res.status(400).json({
                    message:""
                })
            }
            if(!s_token){
                return res.status(400).json({
                    message:"s_token"
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            const store = await Store.findOne({
                storename:storename,
                s_token:s_token
            })
            if(!store){
                return res.status(400).json({
                    message:"s_token"
                })
            }
            if(req.file){//
                store.avatar = req.file.filename//
            }
            store.description = description//
            await store.save()
            return res.json({
                message:""
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),


    //
    app.get('/api/stores',async (req,res)=>{
        try {
            const stores = await Store.find()
            .populate('author','username avatar')//findone,populate
            .sort({"posttime":-1})
            return res.json({
                data:stores
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    }),
    
    //
    app.get('/api/stores/:sid',async (req,res)=>{
        try {
            const {sid} = req.params
            if(!sid){
                return res.status(400).json({
                    message:"ID"
                })
            }
            const store = await Store.findById(sid)
            .populate('author','username avatar description nickname')
            .populate("storecommodities","category title content price posttime pictures")
            .populate("stores",'storename posttime')
            if(!store){
                return res.status(400).json({
                    message:""
                })
            }
            store.password = null
            store.s_token = null
            return res.json({
                data:store
            })
        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    })

    //
    app.delete('/api/stores/:sid',async (req,res)=>{
        try {
            const {sid} = req.params
            if(!sid){
                return res.status(400).json({
                    message:"ID"
                })
            }
            const {username,token} = req.body
            if(!username){
                return res.status(400).json({
                    message:""
                })
            }
            if(!token){
                return res.status(400).json({
                    message:"token"
                })
            }
            const user = await User.findOne({
                username:username,
                token:token
            })
            if(!user){
                return res.status(400).json({
                    message:"token"
                })
            }
            // const newstore = await User.findById(sid)//newstorestore
            // if(!sid){
            //     return res.status(400).json({
            //         message:""
            //     })
            // }
            // if(String(user._id)!==String(newstore.author)){
            //     return res.status(400).json({
            //         message:""
            //     })
            // }
            await Commodity.deleteMany({
                target:mongoose.Types.ObjectId(sid)
            })
            user.stores.pull(sid)
            await user.save()
            return res.json({
                message:""
            })

        } catch (e) {
            return res.status(400).json({
                message:e.message
            })
        }
    })
}

export default storeAPIs