import mongoose from 'mongoose'
import User from './User'

const SecondhandSchema = new mongoose.Schema({
    category:{//
        type:String,
        required:true,
        trim:true
    },
    price:{//
        type:Number,
        required:true
    },
    title:{//
        type:String,
        required:true,
        trim:true
    },
    content:{//
        type:String,
        required:true,
        trim:true
    },
    number:{
        type:Number,
        required:true
    },
    posttime:{//
        type:Date,
        default:Date.now()
    },
    pictures:[{//
        type:String
    }],
    url:String,//
    author:{
        type:mongoose.SchemaTypes.ObjectId,
        required:true,
        ref:'User'
    },
    target:{//id
        type:mongoose.SchemaTypes.ObjectId,
        requried:true,
        ref:'Store'
    },
    comments:[{//
        type:mongoose.SchemaTypes.ObjectId,
        ref:'Comment4'//
    }],
    dianzans_secondhand:[{//
        type:mongoose.SchemaTypes.ObjectId,
        ref:'User'//
    }],
})

const Secondhand = mongoose.model('Secondhand',SecondhandSchema)
export default Secondhand