import mongoose from 'mongoose'
//
const PletterSchema = new mongoose.Schema({
    content:{//
        type:String,
        required:true,
        trim:true
    },
    posttime:{//
        type:Date,
        default:Date.now()
    },
    author:{//
        type:mongoose.SchemaTypes.ObjectId,
        required:true,
        ref:'User'
    },
    target:{//id
        type:mongoose.SchemaTypes.ObjectId,
        requried:true,
        ref:'User'
    },
    // pletters:[{
    //     type:mongoose.SchemaTypes.ObjectId,
    //     ref:'Pletter'
    // }],
    pcomments:[{
        type:mongoose.SchemaTypes.ObjectId,
        ref:'Pcomment'
    }]
})

const Pletter = mongoose.model('Pletter',PletterSchema)
export default Pletter