import mongoose from 'mongoose'

const CommentstoreSchema = new mongoose.Schema({
    content:{//
        type:String,
        required:true,
        trim:true
    },
    posttime:{//
        type:Date,
        default:Date.now()
    },
    anonymous:Boolean,//
    author:{//
        type:mongoose.SchemaTypes.ObjectId,
        requried:true,
        ref:'User'
    },
    target:{//id
        type:mongoose.SchemaTypes.ObjectId,
        requried:true,
        ref:'Storecommodity'
    }
})

const Commentstore = mongoose.model('Commentstore',CommentstoreSchema)
export default Commentstore